(function() {
  'use strict';

  angular
    .module('upflowUi')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $http, ServerConnectService, $rootScope, $state) {

    $log.debug('runBlock end');
    if (ServerConnectService.getObj('TOKEN_KEY') && ServerConnectService.getObj('TOKEN_KEY').token) {
      $http.defaults.headers.common['x-access-token'] = ServerConnectService.getObj('TOKEN_KEY').token;
    }

    $rootScope.$on( '$stateChangeStart', function(e, toState  , toParams
      , fromState, fromParams) {

      var isLogin = toState.name === "login";
      if(isLogin){
        return; // no need to redirect
      }

      // now, redirect only not authenticated

      if(!ServerConnectService.isAuthenticated) {
        e.preventDefault(); // stop current execution
        $state.go('login'); // go to login
      }
    });
  }

})();

(function() {
  'use strict';

  angular
    .module('upflowUi')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('login', {
        url: '/',
        templateUrl: 'app/login/login.html',
        controller: 'LoginController',
        controllerAs: 'login'
      })
      .state('home', {
        url: '/home',
        templateUrl: 'app/home/home.html',
        controller: 'HomeController',
        controllerAs: 'home'
      })
      .state('qa-details', {
        url: '/qa-details/:id',
        templateUrl: 'app/qa_details/qa_details.html',
        controller: 'QADetailsController',
        controllerAs: 'qa'
      })
      .state('profile', {
        url: '/profile',
        templateUrl: 'app/profile/profile.html',
        controller: 'ProfileController',
        controllerAs: 'profile'
      })
      .state('ask', {
        url: '/ask/:search',
        templateUrl: 'app/ask_q/ask_q.html',
        controller: 'AskQController',
        controllerAs: 'ask'
      })
      .state('leaders', {
        url: '/leaders',
        templateUrl: 'app/leaders/leaders.html',
        controller: 'LeadersController',
        controllerAs: 'leader'
      });
    $urlRouterProvider.otherwise('/');
  }

})();

